    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: Poppins, system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

:root {
    --red: #ff3860;
    --red-dark: #ff1443;
    --red-light: #ff5c7c;
    --blue: #498afb;
    --blue-dark: #2674fa;
    --blue-light: #6ca0fc;
    --orange: #fa8142;
    --orange-dark: #f96a1f;
    --orange-light: #fb9865;
    --green: #09c372;
    --green-dark: #07a15e;
    --green-light: #0be586;
    --purple: #9166cc;
    --purple-dark: #7d4bc3;
    --purple-light: #a481d5;
    --pink: #ff4088;
    --pink-dark: #ff1c72;
    --pink-light: #ff649e;
    --gray0: #f8f8f8;
    --gray1: #dbe1e8;
    --gray2: rgb(178, 190, 205);
    --gray3: rgb(108, 121, 131);
    --gray4: #454e56;
    --gray5: #2a2e35;
    --gray5t: #0000005b;
    --gray6: #12181b;
    --gray7: #0b0d0e;
    --font-body: "sofia-pro",sans-serif;
    --font-head: 'Poppins', sans-serif;
    --font-code: "attribute-mono",monospace;
}

body{
    background-color: var(--gray7);
}
header{
    background-color: var(--gray6);
    height: 10vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
nav{
    display: flex;
    justify-content: center;
    gap: 20px;
}
nav>button{
    background-color: white;
    border: none;
    font-size: 1.5em;
    padding: 10px;
    border-radius: 10px;
    font-weight: 700;
}
button.selected{
    background-color: var(--red-light);
}
a{
    color: var(--blue-dark);
}

/* --------------------------------- CONTENT -------------------------------- */

#content{
    min-height: 90vh;
    /* background-image: url(https://static-cse.canva.com/blob/680117/Tangerine_Set6_12.png); */
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size:     100%, auto;                      
    background-repeat:   repeat-y;
    background-position: center start;

    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding-top: 10vh;
}

#contentWrapper{
    background-color: rgb(0,0,0,0.85);
    border-radius: 5px;
    color: white;
    width: clamp(45ch, 50%, 75ch);
    box-sizing: content-box;
    padding: 50px;

    display: flex;
    flex-direction: column;
    gap: 10px;
}

/* ---------------------------------- MENU ---------------------------------- */

.menuWrapper:not(#\\9):not(#\\9){
    width: 50%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.menuWrapper h1{
    grid-row: 1;
    grid-column: 1/4;
    text-align: center;
}

.menuItem{
    border: 3px solid rgba(178, 190, 205, 0.6);
    border-radius: 7px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

.menuItem img{
    width: 100%;
}

/* --------------------------------- CONTACT -------------------------------- */

#contactWrapper{
    border: 3px solid rgba(178, 190, 205, 0.6);
    padding: 10px;
    display: inline;
    width: 35ch;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAEA;IACI,sBAAsB;IACtB,SAAS;IACT,UAAU;IACV,2IAA2I;AAC/I;;AAEA;IACI,cAAc;IACd,mBAAmB;IACnB,oBAAoB;IACpB,eAAe;IACf,oBAAoB;IACpB,qBAAqB;IACrB,iBAAiB;IACjB,sBAAsB;IACtB,uBAAuB;IACvB,gBAAgB;IAChB,qBAAqB;IACrB,sBAAsB;IACtB,iBAAiB;IACjB,sBAAsB;IACtB,uBAAuB;IACvB,eAAe;IACf,oBAAoB;IACpB,qBAAqB;IACrB,gBAAgB;IAChB,gBAAgB;IAChB,2BAA2B;IAC3B,2BAA2B;IAC3B,gBAAgB;IAChB,gBAAgB;IAChB,mBAAmB;IACnB,gBAAgB;IAChB,gBAAgB;IAChB,mCAAmC;IACnC,kCAAkC;IAClC,uCAAuC;AAC3C;;AAEA;IACI,8BAA8B;AAClC;AACA;IACI,8BAA8B;IAC9B,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,uBAAuB;AAC3B;AACA;IACI,aAAa;IACb,uBAAuB;IACvB,SAAS;AACb;AACA;IACI,uBAAuB;IACvB,YAAY;IACZ,gBAAgB;IAChB,aAAa;IACb,mBAAmB;IACnB,gBAAgB;AACpB;AACA;IACI,kCAAkC;AACtC;AACA;IACI,uBAAuB;AAC3B;;AAEA,+EAA+E;;AAE/E;IACI,gBAAgB;IAChB,2FAA2F;IAC3F,yDAA2C;IAC3C,+BAA+B;IAC/B,6BAA6B;IAC7B,iCAAiC;;IAEjC,aAAa;IACb,uBAAuB;IACvB,uBAAuB;IACvB,iBAAiB;AACrB;;AAEA;IACI,iCAAiC;IACjC,kBAAkB;IAClB,YAAY;IACZ,6BAA6B;IAC7B,uBAAuB;IACvB,aAAa;;IAEb,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA,+EAA+E;;AAE/E;IACI,UAAU;IACV,aAAa;IACb,2DAA2D;AAC/D;;AAEA;IACI,WAAW;IACX,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,0CAA0C;IAC1C,kBAAkB;IAClB,aAAa;IACb,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,WAAW;AACf;;AAEA,+EAA+E;;AAE/E;IACI,0CAA0C;IAC1C,aAAa;IACb,eAAe;IACf,WAAW;AACf","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');\r\n\r\n*{\r\n    box-sizing: border-box;\r\n    margin: 0;\r\n    padding: 0;\r\n    font-family: Poppins, system-ui, \"Segoe UI\", Roboto, Helvetica, Arial, sans-serif, \"Apple Color Emoji\", \"Segoe UI Emoji\", \"Segoe UI Symbol\";\r\n}\r\n\r\n:root {\r\n    --red: #ff3860;\r\n    --red-dark: #ff1443;\r\n    --red-light: #ff5c7c;\r\n    --blue: #498afb;\r\n    --blue-dark: #2674fa;\r\n    --blue-light: #6ca0fc;\r\n    --orange